import { useEffect, useState } from "react";
import PortfolioTab from "../portfolioTab/PortfolioTab";
import "./portfolio.scss";
import {
  researchPortfolio,
  interestPortfolio,
  projectPortfolio,
  internshipPortfolio,
  skillPortfolio,
  certificationPortfolio,
} from "../../data";

export default function Portfolio() {
  const [selected, setSelected] = useState("featured");
  const [data, setData] = useState([]);
  const list = [
    {
      id: "interests",
      title: "Interests & Hobbies",
    },
    {
      id: "research",
      title: "Research Papers and Chapters"
    },
    {
      id: "projects",
      title: "Projects",
    },
    {
      id: "internships",
      title: "Internships",
    },
    {
      id: "skills",
      title: "Skills",
    },
    {
      id: "certifications",
      title: "Certifications",
    },
  ];

  useEffect(() => {
    switch (selected) {
      case "interests":
        setData(interestPortfolio);
        break;
      case "research":
        setData(researchPortfolio);
        break;
      case "projects":
        setData(projectPortfolio);
        break;
      case "internships":
        setData(internshipPortfolio);
        break;
      case "skills":
        setData(skillPortfolio);
        break;
      case "certifications":
        setData(certificationPortfolio);
        break;
      default:
        setData(interestPortfolio);
    }
  }, [selected]);

  return (
    <div className="portfolio" id="portfolio">
      <h1>Portfolio</h1>
      <ul>
        {list.map((item) => (
          <PortfolioTab
            title={item.title}
            active={selected === item.id}
            setSelected={setSelected}
            id={item.id}
          />
        ))}
      </ul>
      <div className="container">
        {data.map((d) => (
          <div className="item">
            <img
              src={d.img}
              alt=""
            />
            <h3>{d.title}</h3>
          </div>
        ))}
      </div>
    </div>
  );
}
